body{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.brand-product::after{
    content: "";
    animation: textChanges 10s linear infinite ;
}
.detail-text-image > *{
    margin: 2rem 0px;
}
.product{
    width:100%;
    background-color: #f6f6f6;
    
}
.product-category{
    column-gap:190px;
    row-gap: 20px;
    cursor: pointer;
}
.category-item img{
    height: 250px;
    width: 250px;
}
.feature-details>div{
    width: 200px;

}
.card:hover{
    cursor: pointer;
    transform: scale(1.03);
    transition: .2s;
}
.category-item:hover{
    transform: scale(1.05);
    transition: .25s;
}
.nav-link:hover{
    color:orange;
    transition: .3;
}
#contact{
    background: url("https://webcodershahroz.github.io/shahzadportfolio/static/media/contactbackgrounddark.0e21fb18ef7b5e00ffc9.jpg") no-repeat 50% / cover;
    /* width: 100%; */
}
.address-details{
    background: url("https://webcodershahroz.github.io/shahzadportfolio/static/media/contactbackground.e833793675643eb9cfdb.jpg") no-repeat 50% / cover;
    /* width: 100%; */
}
.mycontainer {
    box-shadow: 1px 1px 15px -4px;
    width: 100%;
    height: 90vh;

}
.related-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
}
#slider {
    height: 100%;
    animation: animate 20s ease-in-out infinite;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

}
@keyframes textChanges {
    0%{
        content: "Quality Locker";
    }
    40%{
        content: "Steel Safes";
    }
    80%{
        content: "Safes";
    }
    100%{
        content:"Safes"
    }
}
@keyframes animate {
    0%,20% {
        background-image: url("./img//img1.jpg");
    }
    20%,40% {
        background-image: url("./img/img2.jpg");
    }
    60%,80% {
        background-image: url("./img/gears-4191907_1280.jpg");
    }
    80%,100%{
        background-image: url("./img/img2.jpg");
    }
    100%,0%{
        background-image: url("./img/img1.jpg");
    }
}
